.row{
    color: white;
    margin-left: 20px;
}
.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    justify-content: space-evenly;
    padding: 20px;
}
.row_posters::-webkit-scrollbar{
    display: none;
}
.row_poster{
    cursor: pointer;
    max-height: 120px;
    object-fit: contain;
    width:100%;
    transition: transform 450ms;
    margin-right: 20px;
}
.row_poster:hover{
    transform: scale(1.08);
    opacity: 1;
}
.row_posterLarge{
    max-height: 250px;
}
.row_posterLarge:hover{
    transform: scale(1.08);
    opacity: 1;
}